<template>
<div class="fx-upload1">
    <!-- <header-text :headerText="headerTitle" @header-event="onHeaderBtn" class="fx-header"></header-text> -->
    <div class="fx-tips">核保资料</div>
    <div class="fx-content" :style="{height:clientHeight+'px'}">
        <div class="subTitle">请上传真实的诊断病历。检验报告等。上传要求： <br/>①病历或报告必须完整页面。 <br/>②检查者姓名和医院盖章需清晰不得覆盖。</div>
        <up-load @return-img="getImg1"></up-load>
    </div>
    <div class="fx-btns1">
        <div class="nextBtn" @click="upData">确认提交</div>
    </div>
</div>
</template>
    
<script>
import Vue from "vue";
import headerText from "@/components/header";
import upLoad from "@/components/upload";
import { Indicator } from 'mint-ui';
var axios = require('axios');
export default {
    name:'upload1',
    data(){
        return{
            orderId:null,
            headerTitle:'资料上传',
            imgList:[],
            clientHeight:0
        }
    },
    created() {

    },
    methods: {
        //点击header
        onHeaderBtn(headerText){
            // console.log(headerText);
        },
        //获取图片
        getImg1(imgList){
            console.log('图片列表1');
            console.log(imgList);
            this.imgList = imgList;
        },
        upData(){
            let QUESTION_PARAMS = JSON.parse(sessionStorage.getItem("QUESTION_PARAMS"));
            console.log("QUESTION_PARAMS",QUESTION_PARAMS);
            console.log("imgList",this.imgList);
            if(this.imgList.length === 0){
                this.$popup.msg("您还未上传核保资料！");
                return
            }
            let imgList = this.imgList;
            imgList.map((item,i)=>{
                QUESTION_PARAMS['upload'+i] = item;
            })
            console.log(QUESTION_PARAMS);
            let THAT = this;
            Indicator.open({
                text: '加载中...',
                spinnerType: 'fading-circle'
            });
            THAT.$http.getHttp({
                name:'fxFormSave',
                params:QUESTION_PARAMS,
                orderId:THAT.orderId,
                type:'post'
            },
            res => {
                Indicator.close();
                // console.log("res",res);
                THAT.$router.push({path:'/fuxing/waiting',query:{"orderId":THAT.orderId}});
            })
        },
        prevBtn(){
            //返回测算页

        }
    },
    computed:{},
    components:{
        "header-text":headerText,
        "up-load":upLoad
    },
    mounted(){
        let orderId = sessionStorage.getItem("ORDER_ID");
        this.orderId = orderId;
        this.clientHeight = window.innerHeight -document.getElementsByClassName("fx-tips")[0].clientHeight-document.getElementsByClassName("fx-btns1")[0].clientHeight-10;
    }
}
</script>

<style lang="scss">
.fx-upload1{
    width:r(750);
    background: #fff;
    .fx-tips{
        height:r(88);
        line-height:r(88);
        font-size:r(33);
        border-bottom:1px solid #dddddd;
        color:#010101;
        font-weight: bold;
        padding-left: r(27);
        background:#f2f2f2;
    }
    .fx-content{
        overflow-y: auto;
        .subTitle{
            line-height:r(44);
            padding-left: r(15);
            font-size: r(32);
            margin:r(21) 0;
            font-weight:normal;
            color: #000;
            .nd{
                display: inline-block;
                text-indent: r(30);
            }
        }
    }
    .fx-btns1{
        height:r(92);
        position:fixed;
        bottom:0;
        width:r(750);
        border-top:1px solid #ddd;
        &>div{
            width:100%;
            height:r(92);
            float:left;
            text-align: center;
            font-size:r(30);
        }
        .nextBtn{
            background:#0084ff;
            color:#fff;
            line-height:r(92);
            height:r(92);
        }
    }
}
</style>


